*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

figure {
  margin: 0;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

p {
  margin: 0;
}

fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

body {
  background-image: url("../assets/wave.svg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Montserrat", sans-serif;
  display: flex;
  flex-direction: column;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0;
}

h1 {
  font-weight: 800;
  font-size: 3.75rem;
  letter-spacing: 5px;
  color: #FDF6F0;
}

main {
  flex-grow: 1;
}

form {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  gap: 100px;
}

.input__box, .output__box {
  padding: 30px 40px 50px;
  background-color: rgba(17, 17, 17, 0.75);
  box-shadow: 3px 3px 12px 0 rgba(17, 17, 17, 0.5);
  border-radius: 5px;
  flex-basis: 45%;
}

h2 {
  color: #FDF6F0;
  font-weight: 700;
  font-size: 2.1875rem;
  text-align: center;
}

.form__field {
  margin-top: 25px;
  width: 100%;
  position: relative;
}
.form__field p {
  font-weight: 400;
  line-height: 1.8;
  font-size: 0.875rem;
  color: #E5E5E5;
  opacity: 0.8;
}
.form__field p strong {
  color: #FB3640;
  letter-spacing: 2px;
  text-transform: uppercase;
}

textarea, input {
  resize: none;
  outline: 0;
  appearance: none;
  width: 100%;
  height: 50px;
  padding: 15px 20px;
  background-color: rgba(0, 34, 51, 0.2);
  border: 2px solid #FDF6F0;
  font-size: 1.125rem;
  font-weight: 600;
  color: #FDF6F0;
  box-shadow: 3px 3px 12px 0 rgba(17, 17, 17, 0.5);
  border-radius: 5px;
}

.input__text {
  height: 100px;
}

.output__text {
  height: 250px;
}

.form__subparent {
  display: flex;
  justify-content: space-between;
}

.form__subfield {
  width: calc(50% - 10px);
  position: relative;
}

button {
  height: 50px;
  width: 100%;
  background-color: #FB3640;
  font-size: 1.125rem;
  font-weight: 700;
  color: #FDF6F0;
  letter-spacing: 2px;
  box-shadow: rgba(17, 17, 17, 0.5);
  border-radius: 5px;
}
button:hover, button:focus, button:active {
  background-color: #fb454e;
}

label {
  position: absolute;
  top: 15px;
  left: 20px;
  font-weight: 500;
  font-size: 0.9375rem;
  color: #FDF6F0;
  pointer-events: none;
  opacity: 0.8;
  transition: top 0.2s ease-in-out, left 0.2s ease-in-out;
}

textarea:focus + label, textarea:active + label, textarea:not(:placeholder-shown) + label, input:focus + label, input:active + label, input:not(:placeholder-shown) + label {
  left: 5px;
  top: -20px;
}

footer {
  padding: 30px 0;
}
footer p, footer small {
  font-size: 1rem;
  font-weight: 400;
  color: #FDF6F0;
}
footer a {
  color: #FB3640;
  font-weight: 600;
  margin-left: 6px;
}
footer a:hover {
  text-decoration: underline;
}
footer svg {
  position: relative;
  top: 6px;
  left: 6px;
}